#titlebox {
    display: flex;
    align-items: center;
    flex-direction: column;
    max-height: 10%;
}

body {
    display: flex;
    flex-direction: column;
}

div#main {
    display: flex;
    flex-direction: row;
}

div.map {
    width: 65%;
    min-width: 600px;
    max-width: 800px;
    // max-height: 80%;
}

svg {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    position: relative;
    width: auto;
}

div#disease-options {
    width: 17.5%;
    min-width: 150px;
    z-index: 1;
}

#disease-options ul {
    position: relative;
    top: -60px;
}

#disease-options ul li {
    display: flex;
    justify-content: center;
    list-style: none;
    padding: 7px;
}

#context {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    position: relative;
    width: 17.5%;
    max-width: 400px;
    min-width: 185px;
    padding-right: 10px;
}

html {
    background-color: #607EA6;
    color: #D0ECF2;
    font-family: sans-serif;
}

#tooltip {
    color: #103778;
    z-index: 3;
}

button {
    border: none;
    background-color: inherit;
    font-size: 16px;
    cursor: pointer;
    display: inline-block;
    font-weight: 700;
    color: inherit;
}
  
  button:hover {
    background: #eee;
    color: black;
}

.slidecontainer {
    width: 100%; 
    position: relative;
    top: -5px;
    left: 3%;
    z-index: 1;
    width: 90%;
}
  
.slider {
    -webkit-appearance: none;
    width: 100%;
    height: 10px;
    border-radius: 5px;  
    opacity: 0.7;
    -webkit-transition: .2s;
    transition: opacity .2s;
    cursor: pointer;
}
  
.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%; 
    background: #204473;
}

div#range {
    padding: 15px;
    padding-bottom: 0px;
    display: flex;
    flex-direction: row;
    top: -12px;
    // left: 230px;
    position: relative;
    div {
        font-size: 16;
        font-weight: 600;

    }
    div#minyear {
        margin-right: 86%;
    }
}

div#links {
    position: absolute;
    display: flex;
    flex-direction: row;
    right: 20px;
}

img {
    width: 40px;
    height: 40px;
    z-index: 137;
    padding: 10px;
}

#modalButton {
    position: absolute;
    margin-left: 10px;
    z-index: 5000;
}

.modal {
    position: absolute;
    display: flex;
    justify-content: center;
    // top: 15px;
    // left: 20px;
    margin-right: 20px;
    border: 10px;
    z-index: 1729;
    overflow: auto;
    width: 50%;
    background-color: rgba(0,0,0,0.4);
}

.modal-content {
    background-color: #607EA6;
    color: #D0ECF2;
    font-weight: 500;
    margin: 5%;
    padding: 15px;
    font-size: 16;
    border: 1px solid #888;
}

head {
    display: flex;
    justify-content: center;
}

.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}
  
.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}